.App {
  text-align: center;
  background: #212121;
}

html, body, #app, #app>div {
  height: 100%;
  overflow: hidden;
}

.App-logo {
  height: 3em;
  width: 3em;
  position: absolute;
}

.App-link {
  color: #09d3ac;
}

.spoiler {
  display: inline;
  line-height: 1em !important;
  color: transparent;
  user-select: none;
  transition: .1s linear;
  border-radius: 5px;
}

.spoiler.shown {
  background: hsla(0, 0%, 100%, .1);
  color: white;
  user-select: initial;
}

.spoiler.hidden {
  background: #202225;
}

.spoiler.hidden:hover {
  background: rgba(32, 34, 37, .8);
  cursor: pointer;
}